.icon-rotate {
    animation: spin 2s linear infinite;
  }
  
  @keyframes spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  span.navbar-brand {
    color: white;
  }
  span.navbar-brand:focus {
    color: white;
  }
  span.navbar-brand:hover {
    color: white;
  }
  svg.icon-rotate.text-light {
    margin-left: 0.5rem;
  }
  button.navbar-toggler {
    color: white;
    margin-right: 0.5rem;
  }
  /* Estilo para quitar el borde en el focus del Navbar.Toggle */
  .navbar-toggler:focus {
  outline: none;
  }
  a.text-light.nav-link {
    margin-left: 0.5rem;
  }
  button.navbar-toggler.collapsed {
    outline: none;
    border: none;
  }
  button.navbar-toggler.collapsed:focus {
    outline: none;
    border: none;
  }
  button.navbar-toggler.collapsed:hover {
    outline: none;
    border: none;
  }